#wrapper-login {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
}
#wrapper-login * {
  transition: 0.3s all;
}
#wrapper-login .left {
  overflow: auto;
  overflow-x: hidden;
  display: flex;
  padding: 2rem;
  justify-self: center;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  position: fixed;
  width: 22rem;
  background-color: #fff;
  border-right: 1px solid #ced4da;
}
