  content: "";
  flex: 1;
  border-bottom: 1px solid #dee2e6;
  margin: 0 1rem;
}
#wrapper-login .left .separator:not(:empty)::before {
  margin-right: 1rem;
}
#wrapper-login .left .separator:not(:empty)::after {
  margin-left: 1rem;
}
#wrapper-login .right {
  padding: 2rem;
  margin-left: 22rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
