  #wrapper-login .right {
    margin-left: 30rem;
    flex: 1;
  }
}
@media (max-width: 767.98px) {
  #wrapper-login .right {
    display: none;
  }
}
#wrapper-login .right .title {
  font-size: 1.7rem;
}
#wrapper-login .right .subtitle {
  font-size: 1.0625rem;
}

